import React from 'react'
import './styles.css'
import { uniqueId } from 'lodash'

import gql from 'graphql-tag'
import { graphql } from 'react-apollo'

import Menu from './components/Menu'
import { StandardButtonStory } from './components/buttons/StandardButton'
import { PagerButtonStory } from './components/pager/PagerButton'
import { PageIndicatorStory } from './components/pager/PageIndicator'
import { PagerStory } from './components/pager/Pager'

import { useQuery } from 'react-apollo-hooks'

const CALENDAR_EVENTS = gql`
	{
		calendarEvents(division: "mt") {
			Id
			Key
			Name
			HostNationCode
			HostNationName
			Category
			Venue
			IsLiveScoring
			LatestOOPDate
		}
	}
`

const App = () => {
	const { data } = useQuery(CALENDAR_EVENTS)

	const events = data.calendarEvents

	return (
		<div className="App">
			<Menu />
			<StandardButtonStory name="Button (Standard)" />
			<PagerButtonStory name="Pager Button" />
			<PageIndicatorStory name="Page Indicator" />
			<PagerStory name="Pager" />
			<div>
				{events && events.map(e => <EventListing key={uniqueId()} {...e} />)}
			</div>
		</div>
	)
}

export default graphql(CALENDAR_EVENTS)(App)

const EventListing = ({
	Caegory,
	HostNationCode,
	HostNationName,
	Id,
	IsLiveScoring,
	Key,
	LatestOOPDate,
	Name,
	Venue,
}) => {
	return (
		<div>
			<h2>{Name}</h2>
		</div>
	)
}
